<template>
    <span :class="'status ' + className + ' color-' + resultStatus.label">
        <Question v-if="resultStatus.id === 1" />
        <YellowCircle v-if="resultStatus.id === 2" />
        <GreenCheck v-if="resultStatus.id === 3" />
        <ErrorCircle v-if="resultStatus.id === 4" />
        {{resultStatus.name}}
    </span>
</template>

<style scoped lang="stylus">
    @require '../guide'
    .status
        text-transform lowercase
        font-size 13px

    .color-success
        color $link-success-color

    .color-attention
        color $link-attention-color

    .color-error
        color $link-error-color

</style>

<script>
import Config from '@/config';

export default {
    name: 'Status',
    props: {
        statusId: {
            type: Number,
            'default': 0
        },
        className: {
            type: String,
            'default': 'else'
        }
    },
    computed: {
        resultStatus() {
            return Config.DOCUMENT_STATUSES.find(({ id }) => id === this.statusId) || { name: 'Неизвестный статус' };
        }
    }
};
</script>




