<template>
    <div class="modal" v-if="visible">
        <div class="modal-fog" />
        <div class="modal-holder">
            <div class="modal-title">
                <button class="modal-close" @click.prevent="handleClose(id)"><ModalClose /></button>
                <slot name="title"></slot>
            </div>
            <div class="modal-body">
                <slot name="body"></slot>
            </div>
        </div>
    </div>
</template>

<style scoped lang="stylus">

    @require '../guide'

    .modal
        position fixed
        display flex
        justify-content center
        align-items center
        top 0
        left 0
        right 0
        bottom 0
        z-index 3
        overflow hidden

        &-fog
            position fixed
            background rgba(0, 0, 0, .3)
            top 0
            left 0
            right 0
            bottom 0

        &-close
            position absolute
            top 30px
            right 30px
            border 0
            background transparent
            cursor pointer

        &-title
            padding-bottom 24px

        &-body
            overflow-x auto
            max-height 90%

        &-holder
            position relative
            vertical-align middle
            width 600px
            height 400px
            overflow hidden
            background white
            border 2px solid $table-selected-color
            padding 30px

</style>

<script>
    export default {
        name: 'Logo',
        props: {
            visible: {
                type: Boolean,
                'default': false
            },
            handleClose: {
                type: Function,
                'default': () => {}
            },
            id: {
                type: String,
                'default': 'no-id'
            }
        },
        watch: {
            visible(visible) {
                document.body.style = visible ? 'overflow: hidden;' : '';
            }
        },
        methods: {
            handleClick() {
            },
            catchScroll() {
                return false;
            }
        }
    };
</script>




