<template>
    <div :class="'holder heading ' + calculateSize + ' ' + className">
        <slot />
    </div>
</template>

<style scoped lang="stylus">
    @font-face
      font-family 'VolvoBroadPro'
      src url('/static/fonts/VolvoBroadPro.eot?#iefix') format('embedded-opentype'),  url('/static/fonts/VolvoBroadPro.otf')  format('opentype'),
             url('/static/fonts/VolvoBroadPro.woff') format('woff'), url('/static/fonts/VolvoBroadPro.ttf')  format('truetype'), url('/static/fonts/VolvoBroadPro.svg#VolvoBroadPro') format('svg')
      font-weight normal
      font-style normal


    .holder
        display flex
        .text
            font-size 20px

    .heading
        .text
            font-weight bold
            font-size 1em

    .size-2
        .text
            font-size 2em

    .heading
        &-volvo
            font-family 'VolvoBroadPro', Helvetica
            font-size 36px
            text-transform uppercase
            padding 46px 0 26px 0

            &-no-top-padding
                padding-top 4px
</style>

<script>
export default {
    name: 'Heading',
    props: {
        size: {
            type: Number,
            'default': 1
        },
        className: {
            type: String,
            'default': ''
        }
    },
    computed: {
        calculateSize() {
            return this.size === 1 ? '' : `size-${this.size}`;
        }
    }
};
</script>




