<template>
    <div class="banner">
        <div class="holder">
            <img :src="image" alt=""/>
        </div>
    </div>
</template>

<style scoped lang="stylus">
    .holder
        width 225px
        height @width
        vertical-align middle
        display table-cell
        text-align center
        overflow hidden
        padding-bottom 27px

        & img
            height 100%
</style>

<script>
export default {
    name: 'Banner',
    props: {
        name: {
            type: String,
            'default': ''
        },
        image: {
            type: String,
            'default': ''
        }
    }
};
</script>




