import * as historyActions from '../actions/history';

export default function undoer({ getState, dispatch }) {
    return next => action => {
        if (!action.type.includes('STATE')) {
            dispatch(historyActions.stateSave({ auth: getState().auth }));
        }
        const result = next(action);
        // TODO: temp solution for testing
        localStorage.setItem('auth', JSON.stringify(getState().auth));
        return result;
    };
}



