import { MODAL_OPEN, MODAL_CLOSE } from '../actions/modal';

const initialState = {
    testingModal: false,
    scheduleModal: false,
    newLegalPerson: false,
    newItem: false,
    newCommonPerson: false
};

export default function history(state = initialState, action) {
    switch (action.type) {
        case MODAL_OPEN:
            return { ...state, [action.id]: true };

        case MODAL_CLOSE:
            return { ...state, [action.id]: false };

        default:
            return state;
    }
}



