<template>
    <div :class="'bar ' + className">
        <div class="line" :style="'width:' + percent + '%'"></div>
    </div>
</template>

<style scoped lang="stylus">
    .bar
        background #dfdfe2
        width 134px
        height 4px
        overflow hidden

    .line
        background #009400
        height 4px
</style>

<script>
import VueTypes from 'vue-types';

export default {
    name: 'ProgressBar',
    props: {
        percent: VueTypes.number.isRequired,
        className: VueTypes.string
    }
};
</script>




