<template>
    <div :class="className">
        <SimpleLink :className="computeClass" :handleClick="processClick">{{label}}</SimpleLink>
        <input hidden ref='file' type="file" @change="processUpload" multiple="true" />
    </div>
</template>

<style scoped lang="stylus">
    .link
        text-decoration underline
        color blue
        font-size 14px
</style>

<script>
import cx from 'classnames';

export default {
    props: {
        label: {
            type: String,
            'default': 'Загрузить...'
        },
        className: {
            type: String,
            'default': ''
        },
        blockId: {
            type: Number,
            'default': ''
        },
        subBlockId: {
            type: Number,
            'default': ''
        },
        updateFileList: {
            type: Function,
            'default': () => {}
        }
    },
    computed: {
        computeClass() {
            return cx([ 'link' ], {
            });
        }
    },
    methods: {
        processClick() {
            this.$refs.file.click();
        },
        processUpload(event) {
            this.updateFileList(this.blockId, this.subBlockId, event.target.files);
        }
    }
};
</script>




