import Vue from 'vue';
import Revue from 'revue';
import { createStore, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import reducer from './reducers/index';
import undoer from './middleware/undoer';
import * as authActions from './actions/auth';
import * as formDocActions from './actions/form-documents';
import * as documentActions from './actions/documents';
import * as requestActions from './actions/requests';
import * as modalActions from './actions/modal';

const createStoreWithMiddleware = applyMiddleware(thunk, undoer)(createStore);

const reduxStore = createStoreWithMiddleware(reducer);

const store = new Revue(Vue, reduxStore, {
    ...authActions,
    ...formDocActions,
    ...documentActions,
    ...requestActions,
    ...modalActions
});

export default store;



