<template>
    <div class="tabs">
        <ul class="tab-list">
            <li v-for="tab in tabList" class="tab-item" @click="handleClick(tab)">
                <slot :name="'tab-' + tab"></slot>
            </li>
        </ul>
        <div class="tab-content">
            <slot :name="'tab-' + selected + '-content'"></slot>
        </div>
    </div>
</template>

<style scoped lang="stylus">
    .tab
        &-list
            list-style none
            padding 0
            margin 0

        &-item
            display inline-block
            padding 10px

        &-item:hover
            cursor pointer
            background-color gray

        &-content
            padding 10px
</style>

<script>
export default {
    name: 'Tabs',
    props: {
        tabsCount: {
            type: Number,
            'default': 0
        }
    },
    data() {
        return {
            selected: 0
        };
    },
    computed: {
        tabList() {
            return Array(this.tabsCount)
                .fill(1)
                .reduce((newArray, singleItem, index) => newArray.concat(index), []);
        }
    },
    methods: {
        handleClick(tabIndex) {
            this.selected = tabIndex;
        }
    }
};
</script>




