<template>
    <a :class="computeClass" :href="href" @click.stop.prevent="processClick"><slot>Link Text</slot></a>
</template>

<style scoped lang="stylus">
    @require '../guide'

    a
        color $link-font-color
        text-decoration-color $link-underline-color

        &.no-border
            border 0
            text-decoration none

        &:visited
            color $link-visited-color
            text-decoration-color $link-underline-color

        &.black
            color black

        &.success
            color $link-success-color
            text-decoration-color $link-success-color

        &.attention
            color $link-attention-color
            text-decoration-color $link-attention-color

        &.link-block
            display block

        &.disabled
            color $gray-font-color
            text-decoration none
            cursor default
            
</style>

<script>
import cx from 'classnames';

export default {
    props: {
        href: {
            type: String,
            'default': ''
        },
        className: {
            type: String,
            'default': ''
        },
        router: {
            type: Boolean,
            'default': false
        },
        block: {
            type: Boolean,
            'default': false
        },
        noline: {
            type: Boolean,
            'default': false
        },
        black: {
            type: Boolean,
            'default': false
        },
        success: {
            type: Boolean,
            'default': false
        },
        attention: {
            type: Boolean,
            'default': false
        },
        disabled: {
            type: Boolean,
            'default': false
        },
        handleClick: {
            type: Function,
            'default': null
        }
    },
    computed: {
        computeClass() {
            return cx([ 'link', this.className ], {
                'link-no-href': !this.href,
                'link-block': this.block,
                'no-border': this.noline,
                disabled: this.disabled,
                black: this.black,
                success: this.success,
                attention: this.attention
            });
        }
    },
    methods: {
        processClick(event) {
            // TODO Обойти эту ошибку - должно правильно пропагироваться событие
            if (event.target.tagName !== 'A' && this.handleClick) {
                event.preventDefault();
                this.handleClick({ target: event.target.parentNode });
                return;
            }
            if (this.disabled) {
                event.preventDefault();
                return;
            }
            if (this.router) {
                event.preventDefault();
                this.$router.push(event.target.getAttribute('href'));
                return;
            }
            if (this.handleClick) {
                event.preventDefault();
                this.handleClick(event);
            }
        }
    }
};
</script>




