<template>
    <i :class="'icon-' + name" :style="style"></i>
</template>

<style scoped lang="stylus">
    @font-face
      font-family 'icomoon'
      src url('/static/fonts/icomoon.ttf?qjd2ug') format('truetype'), url('/static/fonts/icomoon.woff?qjd2ug') format('woff'), url('/static/fonts/icomoon.svg?qjd2ug#icomoon') format('svg')
      font-weight normal
      font-style normal

    [class^="icon-"],
    [class*=" icon-"]
      font-family 'icomoon' !important
      speak none
      font-style normal
      font-weight normal
      font-variant normal
      text-transform none
      line-height 1
      -webkit-font-smoothing antialiased
      -moz-osx-font-smoothing grayscale

    .icon
      &-exit:before
        content: "\e900";

      &-mail:before
        content: "\e901";

      &-phone:before
        content: "\e902";

      &-user:before
        content: "\e903";
</style>

<script>
export default {
    name: 'Icon',
    props: {
        name: {
            type: String,
            'default': ''
        },
        style: {
            type: String,
            'default': ''
        }
    }
};
</script>




