<template>
    <span>
        <input
            ref="input"
            :checked="checked"
            v-bind:value="value"
            v-on:change="updateValue($event.target.value)"
            type="radio"
            :name="name"
        />
        <RadioButton :checked="checked"/>
    </span>
</template>

<style scoped lang="stylus">
    input
        display none
</style>

<script>
export default {
    name: 'FormRadioButton',
    model: {
        prop: 'checked',
        event: 'change'
    },
    props: {
        name: {
            type: String,
            'default': ''
        },
        value: {
            type: String,
            'default': ''
        },
        checked: {
            type: Boolean,
            'default': false
        },
        onChange: {
            type: Function,
            'default': () => {}
        }
    },
    methods: {
        updateValue(value) {
            const formattedValue = value;
            if (formattedValue !== value) {
                this.$refs.input.value = formattedValue;
            }
            // console.log(this.$refs.input, );
            this.$emit('input', formattedValue);
            this.onChange(formattedValue);
        }
    }
};
</script>




