<template>
    <div class="holder">
        <input type="checkbox" v-model="checkbox">
        <svg xmlns="http://www.w3.org/2000/svg" width="21" height="21" viewBox="0 0 21 21">
            <rect @click="handleClick" class="box" x="0.5" y="0.5" width="20" height="20"/>
            <path v-if="checkbox" class="checkmark" d="M624.693,940.668l3.86,3.945,6.754-7.9" transform="translate(-620 -931)"/>
        </svg>
    </div>
</template>

<style scoped lang="stylus">
    input
        display none

    .holder
        display inline

    .box
        fill #fff
        stroke #4d4e53
        stroke-width 1px

    .checkmark
        fill #fff
        stroke #4d4e53
        stroke-width 2px
        fill-rule evenodd

</style>

<script>
import VueTypes from 'vue-types';

export default {
    name: 'CheckBox',
    props: {
        name: VueTypes.string
    },
    data() {
        return {
            checkbox: false
        };
    },
    methods: {
        handleClick() {
            this.checkbox = !this.checkbox;
        }
    }
};
</script>




