<template>
    <small :class="computedClass">
        <slot/>
    </small>
</template>

<style scoped lang="stylus">
    @require '../guide'

    small
        color $gray-font-color
        font-size 14px

        &.smaller
            font-size 12px

        &.small-block
            display block

        &.small-black
            color black
</style>

<script>
    export default {
        name: 'SimpleSmall',
        props: {
            block: {
                type: Boolean,
                'default': false
            },
            black: {
                type: Boolean,
                'default': false
            },
            className: {
                type: String,
                'default': ''
            }
        },
        computed: {
            computedClass() {
                return `${this.block ? 'small-block' : ''} ${this.black ? 'small-black' : ''} ${this.className}`;
            }
        }
    };
</script>




