<template>
    <textarea :name="name" :placeholder="placeholder"/>
</template>

<style scoped>

</style>

<script>
export default {
    name: 'FormTextarea',
    props: {
        placeholder: {
            type: String,
            'default': 'No placeholder'
        },
        name: {
            type: String,
            'default': ''
        }
    }
};
</script>




