import {
    FORM_DOCUMENTS_LOAD,
    FORM_DOCUMENTS_NEW_FILE_ADD,
    FORM_DOCUMENTS_LOAD_SUCCESS,
    FORM_DOCUMENTS_FILE_APPROVE,
    FORM_DOCUMENTS_FILE_REMOVE,
    FORM_DOCUMENTS_FILE_ERROR,
    FORM_DOCUMENTS_LOAD_FAIL
} from '../actions/form-documents';

const initialState = {
    loading: false,
    blocks: [
        {
            name: 'Смирнов Николай Александрович',
            id: 0
        },
        {
            name: 'ООО "Рога и копыта"',
            id: 1
        }
    ],
    subBlocks: [
        {
            name: 'Баланс за 2016 год',
            id: 0,
            block: 0,
            uploaded: false,
            status: 2,
            validThru: '21.06.2017'
        },
        {
            name: 'Устав',
            id: 1,
            block: 0,
            uploaded: false,
            status: 2,
            validThru: '21.06.2017'
        },
        {
            name: 'Баланс за 2016 год',
            id: 2,
            block: 1,
            uploaded: false,
            status: 2,
            validThru: '21.06.2017'
        }
    ],
    fileNames: [
        // {
        //     id: 0,
        //     block: 0,
        //     subBlock: 0,
        //     name: 'balance_2016_p1.pdf',
        //     type: 'pdf'
        // },
    ]
};

function countFilesForSubBlock(files, subBlockId) {
    return files.filter(({ subBlock }) => subBlockId === subBlock);
}

function modifySubBlock(files, subBlockId, operation) {
    return singleBlock => {
        if (singleBlock.id !== subBlockId) {
            return singleBlock;
        }
        const newSubBlock = { ...singleBlock };
        if (operation === 'addFile') {
            newSubBlock.status = 1;
        }

        if (
            operation === 'removeFile' &&
            !countFilesForSubBlock(files, subBlockId).length
        ) {
            newSubBlock.status = 2;
        }

        if (operation === 'approveSubBlock') {
            newSubBlock.status = 3;
        }

        if (operation === 'errorSubBlock') {
            newSubBlock.status = 4;
        }
        return newSubBlock;
    };
}

export default function formDocuments(state = initialState, action) {
    switch (action.type) {
        case FORM_DOCUMENTS_LOAD:
            return { ...state, loading: true };

        case FORM_DOCUMENTS_LOAD_FAIL:
            return { blocks: [], subBlocks: [], fileNames: [], loading: false };

        case FORM_DOCUMENTS_LOAD_SUCCESS:
            return { ...action.response, loading: false };

        case FORM_DOCUMENTS_NEW_FILE_ADD:
            return {
                ...state,
                subBlocks: state.subBlocks.map(
                    modifySubBlock(
                        state.fileNames,
                        action.newFile.subBlock,
                        'addFile'
                    )
                ),
                fileNames: state.fileNames.concat(action.newFile)
            };

        case FORM_DOCUMENTS_FILE_REMOVE: {
            const removingFile = state.fileNames.find(
                ({ id }) => id === action.fileId
            );
            const newFileNames = state.fileNames.filter(
                ({ id }) => id !== action.fileId
            );
            return {
                ...state,
                subBlocks: state.subBlocks.map(
                    modifySubBlock(
                        newFileNames,
                        removingFile.subBlock,
                        'removeFile'
                    )
                ),
                fileNames: newFileNames
            };
        }

        case FORM_DOCUMENTS_FILE_APPROVE: {
            // const changingFile = state.fileNames.find(({ id }) => id === action.fileId);
            const changingFile = state.fileNames[action.fileId];

            return {
                ...state,
                subBlocks: state.subBlocks.map(
                    modifySubBlock(
                        state.fileNames,
                        changingFile.subBlock,
                        'approveSubBlock'
                    )
                )
            };
        }

        case FORM_DOCUMENTS_FILE_ERROR: {
            const changingFile = state.fileNames[action.fileId];

            return {
                ...state,
                subBlocks: state.subBlocks.map(
                    modifySubBlock(
                        state.fileNames,
                        changingFile.subBlock,
                        'errorSubBlock'
                    )
                )
            };
        }

        default:
            return state;
    }
}



