<template>
    <label class="label">
        <div :class="computedLabelClass">
            {{label}}
        </div>
        <div :class="computedValueClass">
            <slot />
        </div>
    </label>
</template>

<style scoped lang="stylus">
    @require '../guide'

    .label
        display flex
        padding 5px 0

        &-name
            min-width 90px
            color $gray-font-color
            display flex
            align-items center

            &-big
                font-size 14px

            &-wide
                min-width 200px

        &-value
            min-width 90px
            width 100%

            &-flex
                display flex
                align-items center

            &-space-between
                justify-content space-between

</style>

<script>
import cx from 'classnames';

export default {
    name: 'FormLabel',
    props: {
        label: {
            type: String,
            'default': ''
        },
        big: {
            type: Boolean,
            'default': false
        },
        spaceBetween: {
            type: Boolean,
            'default': false
        },
        flex: {
            type: Boolean,
            'default': false
        },
        wide: {
            type: Boolean,
            'default': false
        }
    },
    computed: {
        computedLabelClass() {
            return cx('label-name', {
                'label-name-big': this.big,
                'label-name-wide': this.wide
            });
        },
        computedValueClass() {
            return cx('label-value', {
                'label-value-flex': this.flex,
                'label-value-space-between': this.spaceBetween
            });
        }
    }
};
</script>




