export const REQUEST_LOAD = 'REQUEST_LOAD';
export const REQUEST_LOAD_SUCCESS = 'REQUEST_LOAD_SUCCESS';
export const REQUEST_LOAD_FAIL = 'REQUEST_LOAD_FAIL';

export function requestsLoading() {
    return { type: REQUEST_LOAD };
}

export function requestsLoaded(response) {
    return { type: REQUEST_LOAD_SUCCESS, response };
}

export function requestsLoad() {
    return dispatch => {
        dispatch(requestsLoading());
        fetch('http://beta.json-generator.com/api/json/get/E1PqkhQnG', {
            credentials: 'same-origin',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then(response => response.json())
            .then(data => dispatch(requestsLoaded(data)));
    };
}



