<template>
    <table :class="composeClass">
        <thead>
            <tr>
                <td v-for="heading in headings" >
                    <SimpleSmall className='smaller'>{{heading.title}}</SimpleSmall>
                </td>
            </tr>
        </thead>
        <slot />
    </table>
</template>

<style scoped lang="stylus">

    table
        border-collapse separate 
        border-spacing 0 12px

        &.table-full-width
            width 100%

    thead 
        tr
            margin-bottom -5px
        td
            padding 5px 10px 0

    tbody
        td
            padding 10px

        tr
            cursor pointer
            background-color white
            vertical-align middle
            box-shadow 0 0 9px 2px rgba(0, 0, 0, .1)

</style>

<script>
export default {
    name: 'SimpleTable',
    props: {
        headings: {
            type: Array,
            'default': () => [ { title: 'Column 1', key: 'column1' }, { title: 'Column 2', key: 'column2' } ]
        },
        sortKey: {
            type: String,
            'default': ''
        },
        fullWidth: {
            type: Boolean,
            'default': false
        },
        sortDir: {
            type: String,
            'default': 'asc'
        },
        filterValue: {
            type: String,
            'default': ''
        }
    },
    computed: {
        composeClass() {
            let baseClass = 'table ';
            if (this.fullWidth) {
                baseClass += ' table-full-width ';
            }
            return baseClass;
        }
    },
    methods: {
        getValue(searchedKey, row) {
            const currentValue = row.cells.find(({ key }) => key === searchedKey);
            return currentValue ? currentValue.value : '-';
        }
    }
};
</script>




