export const DOCUMENTS_LOAD = 'DOCUMENTS_LOAD';
export const DOCUMENTS_LOAD_SUCCESS = 'DOCUMENTS_LOAD_SUCCESS';
export const DOCUMENTS_LOAD_FAIL = 'DOCUMENTS_LOAD_FAIL';

export function documentsLoading() {
    return { type: DOCUMENTS_LOAD };
}

export function documentsLoaded(response) {
    return { type: DOCUMENTS_LOAD_SUCCESS, response };
}

export function documentsLoad() {
    return dispatch => {
        dispatch(documentsLoading());
        fetch('http://beta.json-generator.com/api/json/get/E1PqkhQnG', {
            credentials: 'same-origin',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then(response => response.json())
            .then(data => dispatch(documentsLoaded(data)));
    };
}



