<template>
    <div :class="'card ' + (wide ? 'card-wide' : '')" :id="id">
        <div class="card-header" @click="toggleCard">
            <div class="card-status">
                <StatusBinary :successState="state" />
            </div>
            <div class="card-header-text"><FoldableArrow :opened="opened" />&nbsp;&nbsp;{{name}} <Badge v-if="count">{{count}}</Badge></div>
        </div>
        <div :class="bodyClass">
            <slot></slot>
        </div>
        <div class="card-footer">
            <slot name="card-footer"/>
        </div>
    </div>
</template>

<style scoped lang="stylus">
    @require '../guide'

    .card
        width 550px
        display inline-block
        margin-right 30px
        margin-bottom 30px
        background-color white
        box-shadow 0 0 9px 2px rgba(0, 0, 0, .1)

        &-wide
            width 100%

        &-body
            padding 20px

            &-closed
                display none

        &-header
            padding 10px 15px
            border-bottom 1px solid $light-gray-separator-color
            position sticky
            top 0px
            background-color white
            z-index 2
            cursor pointer

            &-text
                font-size 16px
                font-weight bold

        &-status
            float right

        &-footer 
            & > div
                border-top 1px solid $light-gray-separator-color
                display flex
                justify-content center
                align-items center

                & > a
                    text-align center
                    padding 10px
                    width 100%
                    font-size 14px

                    &:not(:last-child)
                        border-right 1px solid $light-gray-separator-color

                    &:only-child
                        border 0

    .centered
        padding 0
        width 370px
        height auto

        .card-body
            padding 0
</style>

<script>
import Badge from '@/components/Badge';

import cx from 'classnames';

export default {
    name: 'FoldableCard',
    components: {
        Badge
    },
    data() {
        return {
            opened: true
        };
    },
    props: {
        name: {
            type: String,
            'default': ''
        },
        string: {
            type: String,
            'default': ''
        },
        count: {
            type: Number,
            'default': 0
        },
        id: {
            type: String,
            'default': ''
        },
        wide: {
            type: Boolean,
            'default': false
        },
        state: {
            type: Boolean,
            'default': false
        }
    },
    computed: {
        bodyClass() {
            return cx([ 'card-body' ], {
                'card-body-closed': !this.opened
            });
        }
    },
    methods: {
        toggleCard() {
            this.opened = !this.opened;
        }
    }
};
</script>




