<template>
    <CheckBox />
</template>

<style scoped lang="stylus">
</style>

<script>
// <input
//         ref="input"
//         :checked="value"
//         v-bind:value="value"
//         v-on:change="updateValue($event.target.value)"
//         type="checkbox"
//         :name="name"
//     />
export default {
    name: 'FormCheckbox',
    model: {
        prop: 'checked',
        event: 'change'
    },
    props: {
        name: {
            type: String,
            'default': ''
        },
        value: {
            type: Boolean,
            'default': false
        }
    },
    methods: {
        updateValue(value) {
            const formattedValue = value;
            if (formattedValue !== value) {
                this.$refs.input.value = formattedValue;
            }
            this.$emit('input', Boolean(formattedValue));
        }
    }
};
</script>




