<template>
    <svg xmlns="http://www.w3.org/2000/svg" width="18.5" height="18.5" viewBox="0 0 18.5 18.5">
        <circle class="svg-radio-circle" cx="9.25" cy="9.25" r="8.5"/>
        <circle :class="checkboxClass" cx="0" cy="0" r="4"/>
    </svg>
</template>

<style lang="stylus">
    .svg-radio-circle
        fill #fff
        stroke #4d4e53
        stroke-width 1.5px

    .svg-radio-dot
        fill #373a4b
        transform translate(9px, 9px)
        &.hidden
            display none
</style>

<script>

export default {
    name: 'RadioButton',
    props: {
        checked: {
            type: Boolean,
            'default': false
        }
    },
    computed: {
        checkboxClass() {
            return `svg-radio-dot ${(this.checked ? '' : 'hidden')}`;
        }
    }
};
</script>



