<template>
    <div :class="'card ' + className ">
        <div class="card-body" v-if="$slots['card-body']">
            <slot name="card-body"/>
        </div>
        <div class="card-footer" v-if="$slots['card-footer']">
            <slot name="card-footer" />
        </div>
    </div>
</template>

<style scoped lang="stylus">
    @require '../guide'

    .card
        width 260px
        height 100%
        display inline-block
        margin-right 30px
        margin-bottom @margin-right
        background-color white
        box-shadow 0 0 9px 2px rgba(0, 0, 0, .1)

        &:hover
            box-shadow 0 0 9px 2px rgba(0, 0, 0, .4)

        &-body
            padding 20px

        &-footer
            background $gray-background-color
            box-shadow inset 0 5px 9px 0 rgba(0, 0, 0, .05)

        &-footer
            table
                font-size 12px
                padding 15px 0

                td
                    padding 3px 20px

    .centered
        padding 0
        width 375px
        height auto

        .card-body
            padding 0
</style>

<script>


export default {
    name: 'Card',
    props: {
        className: {
            type: String,
            'default': ''
        }
    }
};
</script>




