        justify-content: center;
        align-items: center;
        padding-right: 20px;
    }
    .login-bar .user-name {
        white-space: nowrap;
    }

    .exit-button {
        text-align: right;
        border-left: 1px solid #e8e8e8;
        padding: 28px 0 20px 40px;
        margin-left: 27px;
