        &-subtext
            position absolute
            top 42px
</style>

<script>
import { requestsLoad } from '@/actions/requests';
import { documentsLoad } from '@/actions/documents';
import { logout } from '@/actions/auth';

import store from './store';

export default {
    name: 'app',
    data() {
        return {
            auth: this.$select('auth'),
            requests: this.$select('requests') || {},
            documents: this.$select('documents') || {}
        };
    },
    mounted() {
        store.dispatch(documentsLoad());
        store.dispatch(requestsLoad());
    },
    methods: {
        handleExit() {
            store.dispatch(logout());
            this.$router.push('/login');
        },
        updateUser() {

        }
    }
};
</script>



