<template>
    <button :disabled="disabled" @click="handleClick" :class="computedClass">
        <slot>
            Button
        </slot>
    </button>
</template>

<style scoped lang="stylus">
    @require '../guide'

    button
        outline none
        border 2px solid $table-selected-color
        cursor pointer
        background-color white
        font-size 14px
        padding 8px 10px

        &:not(.disabled):hover
            background-color transparent

        &:active
            box-shadow inset 0 0 2px 2px rgba(0, 0, 0,.3)

        &.disabled
            cursor default
            border-color $gray-darker-border-color
            color $gray-darker-border-color

</style>

<script>
    import cx from 'classnames';

    export default {
        name: 'SimpleButton',
        props: {
            onClick: {
                type: Function,
                'default': () => {}
            },
            disabled: {
                type: Boolean,
                'default': false
            },
            className: {
                type: String,
                'default': ''
            }
        },
        methods: {
            handleClick() {
                return this.onClick();
            }
        },
        computed: {
            computedClass() {
                return cx([ this.className ], {
                    disabled: this.disabled
                });
            }
        }
    };
</script>




