export const STATE_SAVE = 'STATE_SAVE';
export const STATE_POP = 'STATE_POP';
export const STATE_CLEAR = 'STATE_CLEAR';

export function stateSave(lastState) {
    return { type: STATE_SAVE, lastState };
}

export function statePop() {
    return { type: STATE_POP };
}

export function stateClear() {
    return { type: STATE_CLEAR };
}



