import {
    DOCUMENTS_LOAD,
    DOCUMENTS_LOAD_SUCCESS,
    DOCUMENTS_LOAD_FAIL
} from '../actions/documents';

const initialState = { loading: false, list: [] };

export default function documents(state = initialState, action) {
    switch (action.type) {
        case DOCUMENTS_LOAD:
            return { ...state, loading: true };

        case DOCUMENTS_LOAD_FAIL:
            return { list: [], loading: false };

        case DOCUMENTS_LOAD_SUCCESS:
            return { list: action.response.slice(0, 2), loading: false };

        default:
            return state;
    }
}



