<template>
  <div class="calculations">
    <Heading className="heading-volvo">
        Варианты предложений
    </Heading>

    <InteractiveTable :headings="offers.headings" fullWidth>
        <tbody @click="handleRowClick">
            <tr v-for="row in requests.variants" :class="selectedItem === row.id ? 'selected-row' : ''">
                <td class="checker-col">
                    <FormRadioButton
                        :onChange="handleSelectionChange"
                        :checked="selectedItem === row.id"
                        :value="row.id"
                        name="offers" />
                </td>
                <td class="item-col">{{row.name}}</td>
                <td>{{row.amount}} шт.</td>
                <td>{{row.period}} мес.</td>
                <td>{{row.forepay}}%</td>
                <td><Currency :amount="row.price" :precision="0"/></td>
                <td><Currency :amount="row.total" :precision="0"/></td>
                <td>{{row.withhold}} %</td>
            </tr>
        </tbody>
    </InteractiveTable>

    <RequestBudgetItem :item="selectedOptionData"/>

    <SimpleButton :onClick="validateForm">Акцептовать предложение</SimpleButton>
  </div>
</template>

<style scoped lang="stylus">
    @require '../../guide'
    .checker-col
        width 10px
        padding 10px 0 10px 10px
        text-align center

    .item-col
        min-width 157px

    .calculations
        width $main-width - 190
        padding-bottom 10px

    .selected-row
        background-color $table-selected-color
        color white

    .heading-volvo
        padding 22px 0 6px

</style>

<script>
import store from '@/store';
import { modalClose, modalOpen } from '@/actions/modal';
import RequestBudgetItem from '@/pages/containers/RequestBudgetItem';

const initialState = {
};

export default {
    name: 'RequestBudget',
    components: {
        RequestBudgetItem
    },
    computed: {
        selectedOptionData() {
            return this.requests.options.find(({ id }) => id === this.selectedItem);
        }
    },
    data() {
        return {
            form: { ...initialState },
            requests: this.$select('requests') || {},
            modal: this.$select('modal') || {},
            optionsData: [],
            variants: [],
            selectedItem: '',
            offers: {
                headings: [
                    { title: '', key: 'column0' },
                    { title: 'Предметы', key: 'column1' },
                    { title: 'Кол-во', key: 'column2' },
                    { title: 'Срок', key: 'column3' },
                    { title: 'Аванс', key: 'column4' },
                    { title: 'Стоимость', key: 'column5' },
                    { title: 'Сумма плетежей', key: 'column6' },
                    { title: 'Удержание', key: 'column7' }
                ]
            }
        };
    },
    props: {
        requestId: {
            type: String,
            'default': ''
        }
    },
    methods: {
        handleModalClose(modalId) {
            store.dispatch(modalClose(modalId));
        },
        handleSelectionChange(newValue) {
            this.selectedItem = newValue;
        },
        openScheduleModal(event) {
            event.preventDefault();
            store.dispatch(modalOpen('scheduleModal'));
        },
        handleRowClick(event) {
            const { target } = event;
            if (target.tagName === 'TD') {
                target.parentNode.querySelector('input[type="radio"]').click();
            }
        },
        validateForm() {
            // eslint-disable-next-line
            console.log(this.form);
            this.form = { ...this.form, ...initialState };
        }
    }
};
</script>




