<template>
    <span :class="className">
        {{formattedPrice}}
    </span>
</template>

<script>
import currencyFormatter from 'currency-formatter';

export default {
    name: 'Currency',
    props: {
        amount: {
            type: Number,
            'default': 0
        },
        precision: {
            type: Number,
            'default': 2
        },
        className: {
            type: String,
            'default': ''
        }
    },
    computed: {
        formattedPrice() {
            return currencyFormatter.format(this.amount, { code: 'RUB', precision: this.precision });
        }
    }
};
</script>




