<template>
    <div class="page">
        <Heading v-if="title" :size="2">{{title}}</Heading>
        <slot />
    </div>
</template>

<style scoped lang="stylus">

    @require '../guide'

    .page
        padding 0 0 20px 0
        margin 0 auto
        min-height 800px
        width $main-width
        position relative

</style>

<script>

import Heading from '@/components/Heading';

export default {
    components: {
        Heading
    },
    name: 'Page',
    props: {
        title: {
            type: String,
            'default': ''
        }
    }
};
</script>




