<template>
    <input
        :class="computedClass"
        ref="input"
        v-bind:value="value"
        v-on:input="updateValue($event.target.value)"
        :name="name"
        :type="type"
        :placeholder="placeholder"/>
</template>

<style scoped lang="stylus">
    @require '../guide'

    input
        display inline-block
        padding 8px
        width 100%
        font-size 14px
        border 1px solid $gray-border-color

    .invalid
        border-color red

    .short
    .inline
        margin-right 10px

    .short
        width 110px

</style>

<script>
import cx from 'classnames';

export default {
    name: 'FormInput',
    props: {
        placeholder: {
            type: String,
            'default': ''
        },
        name: {
            type: String,
            'default': ''
        },
        invalid: {
            type: Boolean,
            'default': false
        },
        inline: {
            type: Boolean,
            'default': false
        },
        value: {
            type: String,
            'default': ''
        },
        big: {
            type: Boolean,
            'default': false
        },
        'short': {
            type: Boolean,
            'default': false
        },
        type: {
            type: String,
            'default': 'text'
        }
    },
    methods: {
        updateValue(value) {
            const formattedValue = value;
            if (formattedValue !== value) {
                this.$refs.input.value = formattedValue;
            }
            this.$emit('input', formattedValue);
        }
    },
    computed: {
        computedClass() {
            return cx('', {
                big: this.big,
                inline: this.inline,
                invalid: this.invalid,
                'short': this.short
            });
        }
    }
};
</script>




