import Vue from 'vue';
import Router from 'vue-router';
// import Main from '@/pages/Main';
import Sink from '@/pages/Sink';
import Map from '@/pages/Map';
import Dashboard from '@/pages/Dashboard';
import Request from '@/pages/Request';
// import test from '@/directives/test';
import Login from '@/pages/Login';
import store from '@/store';

Vue.use(Router);
// Vue.directive('test', test);
export const routerSettings = [
    { path: '/login', name: 'Login', component: Login },
    { path: '/', name: 'Dashboard', component: Dashboard },
    { path: '/sink', name: 'Sink', component: Sink },
    { path: '/map', name: 'Map', component: Map },
    {
        path: '/request/:id/*',
        example: '/request/1',
        name: 'Request',
        component: Request
    }
];
const router = new Router({ routes: routerSettings });

router.beforeEach((to, from, next) => {
    if (to.name !== 'Login' && !store.store.getState().auth.authenticated) {
        next('/login');
        return;
    }
    next();
});

export default router;



