<template>
    <li :class="'file-list-item ' + (progress < 100 ? 'loading' : '')">
        <PdfFile v-if="file.type === 'pdf'"/>
        <WordFile v-if="file.type === 'doc'"/>
        <ImageFile v-if="['png', 'jpg', 'jpeg'].includes(file.type)" :preview="preview" />
        <SimpleLink className="file-list-item-name" :href="file.name">
            <span class="file-list-item-name-text">{{file.name}}</span>
            <ProgressBar v-if="progress < 100" className="progress-bar" :percent="progress"/>
        </SimpleLink>
        <SimpleLink v-if="progress === 100" :handleClick="removeFile(file.id)"><RedCross/></SimpleLink>
    </li>
</template>

<style scoped lang="stylus">
    .file-list-item
        padding 16px 0
        margin-right 32px
        width 225px
        display flex
        align-items center

        &-name
            padding 0 10px

        &.loading
            svg, .file-list-item-name-text
                opacity .4

        &-name
            padding 0 10px
            font-size 14px
            position relative

    .progress-bar
        bottom -7px
        right 0
        left 10px
        position absolute
</style>

<script>
import VueTypes from 'vue-types';

export default {
    name: 'SingleFile',
    props: {
        file: VueTypes.object,
        removeFile: VueTypes.function
    },
    data() {
        return {
            progress: 0,
            preview: '',
            loadingInterval: null
        };
    },
    mounted() {
        const reader = new FileReader();
        reader.onloadend = () => {
            this.preview = reader.result;
        };
        if (this.file.originalFile) {
            reader.readAsDataURL(this.file.originalFile);
        }
        if (this.file.newFile) {
            this.loadingInterval = setInterval(() => {
                this.progress = this.progress + Math.floor(Math.random() * 10);
                if (this.progress >= 100) {
                    this.progress = 100;
                    clearInterval(this.loadingInterval);
                }
            }, 200);
        }
        else {
            this.progress = 100;
        }
    },
    beforeDestroy() {
    },
    methods: {
    }
};
</script>




