<template>
  <Page>
    <div class="main-body">
        <div class="main-table">
            <div class="single-table">
                <nav>
                    <SimpleLink
                        router
                        :className="status === 'active' ? 'current-nav' : ''"
                        :disabled="!activeLength"
                        href="?status=active">{{$t("pages.dashboard.tabs.active")}} <Badge v-if="activeLength">{{activeLength}}</Badge></SimpleLink>
                    <SimpleLink
                        router
                        :className="status === 'inprogress' ? 'current-nav' : ''"
                        :disabled="!inProgressLength"
                        href="?status=inprogress">{{$t("pages.dashboard.tabs.waiting")}} <Badge v-if="inProgressLength">{{inProgressLength}}</Badge></SimpleLink>
                    <SimpleLink
                        router
                        :className="status === 'approved' ? 'current-nav' : ''"
                        :disabled="!approvedLength"
                        href="?status=approved">{{$t("pages.dashboard.tabs.approved")}} <Badge v-if="approvedLength">{{approvedLength}}</Badge></SimpleLink>
                    <SimpleLink
                        router
                        :className="status === 'archived' ? 'current-nav' : ''"
                        :disabled="!archivedLength"
                    href="?status=archived">{{$t("pages.dashboard.tabs.archive")}} <Badge v-if="archivedLength">{{archivedLength}}</Badge></SimpleLink>
                </nav>
                <Heading className="heading-volvo">{{ $t("pages.dashboard.requests") }}</Heading>
                <RequestsList :requests="filteredRequeests(requests)" />
            </div>
            <div class="single-table">
                <Heading className="heading-volvo">{{ $t("pages.dashboard.agreement") }}</Heading>
                <RequestsList shortCard :requests="documents" />
            </div>
        </div>
        <div class="banner-holder">
            <div class="single-table">
                <Heading className="heading-volvo">
                    {{ $t("pages.dashboard.specials") }}
                </Heading>
                <Banner image="./static/banner-1.png" />
                <Banner image="./static/banner-2.png" />
            </div>
        </div>
    </div>
  </Page>
</template>

<style scoped lang="stylus">
    @require '../guide'

    .main-body
        width 1082px
        max-width @width
        margin 0 auto

    .main-body, .table-heading
        display flex
        justify-content space-between

    .table-heading .holder.heading
        min-width 100px

    .main-table
        min-width 77%


    .banner-holder
        text-align center
        display inline-block

    nav
        position absolute
        top 44px
        height 30px
        left 178px
        width 520px
        display flex
        justify-content space-between
        align-items center

        .current-nav
            position relative
            z-index 2
            &::after
                content: ''
                background #e4e6ea
                position absolute
                top: -7px
                left: -13px
                right: -18px
                bottom: -7px
                display block
                z-index -1

    .single-table
        position relative

    .card-holder
        vertical-align top
        display flex
        flex-wrap wrap
</style>

<script>
    import store from '@/store';

    // i18n.locale = 'en';

    export default {
        name: 'dashboard',
        data() {
            return {
                modal: this.$select('modal') || {},
                requests: this.$select('requests') || {},
                documents: this.$select('documents') || {}
            };
        },
        computed: {
            status() {
                return this.$route.query.status || 'active';
            },
            archivedLength() {
                console.log(this.requests);
                return this.filteredRequeests(this.requests, 'archived').list.length;
            },
            activeLength() {
                return this.filteredRequeests(this.requests, 'active').list.length;
            },
            inProgressLength() {
                return this.filteredRequeests(this.requests, 'inprogress').list.length;
            },
            approvedLength() {
                return this.filteredRequeests(this.requests, 'approved').list.length;
            }
        },
        mounted() {
            store.reduxActions.requestsLoad();
            store.reduxActions.documentsLoad();
        },
        methods: {
            filteredRequeests(requests, stat = this.status) {
                return {
                    loading: requests.loading,
                    list: requests.list.filter(({ status }) => status === stat)
                };
            }

        }
    };
</script>



