<template>
    <select :name="name">
        <option v-for="singleOption in values" :value="singleOption.value">{{singleOption.name}}</option>
    </select>
</template>

<style scoped>

</style>

<script>
export default {
    name: 'FormSelect',
    props: {
        name: {
            type: String,
            'default': ''
        },
        values: {
            type: Array,
            'default': []
        }
    }
};
</script>




