<template>
    <transition name="fade">
        <button @click="handleClick" v-if="visible">
            <ArrowUp class="arrow-up"/>
            <div class="text">
                <slot>
                    Button
                </slot>
            </div>
        </button>
    </transition>
</template>

<style scoped lang="stylus">
    .arrow-up 
        display block
        text-align center
        position relative
        top -10px
        left 40px

    button
        position fixed
        text-align center
        bottom 22px
        right 108px
        background white
        cursor pointer
        outline none
        border 0
        width 108px
        height 80px
        box-shadow 0 0 9px 2px rgba(0, 0, 0, .1)

    .text
        position relative
        top 5px

    .fade-enter-active, .fade-leave-active
        transition: opacity .2s

    .fade-enter, .fade-leave-to
        opacity: 0

</style>

<script>
import jump from 'jump.js';
import VueTypes from 'vue-types';

export default {
    name: 'BackToTop',
    props: {
        onClick: VueTypes.function
    },
    data() {
        return {
            visible: false
        };
    },
    mounted() {
        document.addEventListener('scroll', this.handleScroll);
    },
    beforeDestroy() {
        document.removeEventListener('scroll', this.handleScroll);
    },
    methods: {
        handleClick() {
            jump('.app');
        },
        handleScroll() {
            const newState = document.body.scrollTop > window.screen.height / 2;
            if (newState !== this.visible) {
                this.visible = newState;
            }
        }
    }
};
</script>




