<template>
    <table :class="composeClass">
        <thead>
            <tr>
                <td v-for="heading in headings" >
                    {{heading.title}}
                </td>
            </tr>
        </thead>
        <tbody>
            <slot />
            <tr v-for="row in rows" :class="row.className">
                <td v-for="heading in headings" >
                    {{getValue(heading.key, row)}}
                </td>
            </tr>
        </tbody>
    </table>
</template>

<style scoped lang="stylus">
    @require '../guide'

    .table-full-width
        width 100%

    table, td
        border-collapse collapse

    thead td
        color $gray-font-color

    table.small
        font-size 14px
        border-spacing 0 10px

    td
        padding 5px 0
        width 50%

    td.gray-cell
        color $gray-font-color
        width 20px

</style>

<script>
export default {
    name: 'SimpleTable',
    props: {
        headings: {
            type: Array,
            'default': () => [ { title: 'Column 1', key: 'column1' }, { title: 'Column 2', key: 'column2' } ]
        },
        rows: {
            type: Array,
            'default': () => [
                { className: '1', cells: [ { key: 'column1', value: '<a>asd</a>' }, { key: 'column2', value: 'Column 2 data 1' } ] },
                { cells: [ { key: 'column1', value: 'Column 1 data 2' }, { key: 'column2', value: 'Column 2 data 2' } ] }
            ]
        },
        sortKey: {
            type: String,
            'default': ''
        },
        className: {
            type: String,
            'default': ''
        },
        fullWidth: {
            type: Boolean,
            'default': false
        },
        sortDir: {
            type: String,
            'default': 'asc'
        },
        filterValue: {
            type: String,
            'default': ''
        }
    },
    computed: {
        composeClass() {
            let baseClass = `table ${this.className}`;
            if (this.fullWidth) {
                baseClass += ' table-full-width ';
            }
            return baseClass;
        }
    },
    methods: {
        getValue(searchedKey, row) {
            const currentValue = row.cells.find(({ key }) => key === searchedKey);
            return currentValue ? currentValue.value : '-';
        }
    }
};
</script>




