<template>
  <div>
     <Heading className="heading-volvo">Анкеты</Heading>
    <Card>
        <div slot="card-body">
            <SimpleLink className="list-link" noline router :href="'/request/'+ requestId + '/forms?formId=2'">
                Смирнов Николай Александрович
            </SimpleLink>
            <div>ИНН 123123123</div>
        </div>
        <div slot="card-footer">
            <SimpleLink noline className="list-footer" attention>Заполнено на 75%</SimpleLink>
        </div>
    </Card>
  </div>
</template>

<style scoped lang="stylus">
    .heading-volvo
        padding 22px 0 20px

    .list
        &-link
            font-size 20px
            font-weight bold
            display inline-block
            padding-bottom 10px

        &-footer
            font-size 12px
            padding 15px
            display block
</style>

<script>
import store from '@/store';
import { modalClose, modalOpen } from '@/actions/modal';

const initialState = {
};

export default {
    name: 'map',
    data() {
        return {
            form: { ...initialState },
            modal: this.$select('modal') || {}
        };
    },
    props: {
        requestId: {
            type: String,
            'default': ''
        }
    },
    methods: {
        handleModalClose(modalId) {
            store.dispatch(modalClose(modalId));
        },
        openAddCommonPerson(event) {
            event.preventDefault();
            store.dispatch(modalOpen('newCommonPerson'));
        },
        validateForm() {
            // eslint-disable-next-line
            console.log(this.form.sameAddress);
            this.form = { ...this.form, ...initialState };
        }
    }
};
</script>




