<template>
  <div class="two-fold">
    <div class="main-body">
        <Heading className="heading-volvo">Документы</Heading>
        <LoadingAnimation v-if="loading" />
        <FoldableCard
            wide
            :key="block.id"
            v-for="block in modifiedBlocks"
            :name="block.name"
            :state="block.state"
            :id="getHash(block.id)">
            <div
                :class="subSectionClass(subBlock.status)"
                v-for="subBlock in subBlockById(block.id)" :id="getHash([block.id])">
                <div class="subblock-heading">
                    <b>{{subBlock.name}}</b>&nbsp;&nbsp;&nbsp;&nbsp;<Status :statusId="subBlock.status" />
                    <SimpleSmall className="subblock-heading-period">
                        дейсвует до {{subBlock.validThru}}
                    </SimpleSmall>
                </div>
                <ul class="file-list">
                    <SingleFile :removeFile="removeFile" :file="file" :key="file.id" v-for="file in filesByBlockSubblock(block.id, subBlock.id)" />
                </ul>
                <FormFileUpload
                    classList="upload-block"
                    :label="filesByBlockSubblock(block.id, subBlock.id).length ? 'Загрузить еще...' : 'Загрузить...'"
                    :blockId="block.id"
                    :subBlockId="subBlock.id"
                    :updateFileList="updateFileList"/>
            </div>
        </FoldableCard>
        <SimpleButton disabled :onClick="validateForm">Подтвердить загрузку документов</SimpleButton>
        <SimpleButton :onClick="documentsSuccess">Одобрить случайный документ</SimpleButton>
        <SimpleButton :onClick="documentsFail">Случайная ошибка</SimpleButton>
    </div>
    <div class="side-body">
        <QuickNav :list="modifiedBlocks"/>
    </div>
  </div>
</template>

<style scoped lang="stylus">
    @require '../../guide'

    .heading-volvo
        padding-top 0
        padding-bottom 23px

   .two-fold
        padding-top 20px
        display flex
        width 1086px

    .main-body
        width 740px

    .side-body
        width 300px
        padding-top 43px
        padding-left 110px

    .subblock-heading
        b
            font-size 14px
        &-period
            float right

    .subblock-section
        padding 18px 20px
        margin 0 -20px

        &:not(:first-child)
            border-top 1px solid $light-gray-separator-color

        &-error
            background-color $background-error
            &:not(:first-child)
                border-top 1px solid $border-error

        &-attention
            background-color $background-attention

            &-no-bg
                background-color transparent

            &:not(:first-child)
                border-top 1px solid $border-attention

        &:first-child
            margin-top: -24px

        &:last-child
            margin-bottom: -20px

    .file-list
        list-style none
        margin 0
        padding 0
        display flex
        flex-wrap wrap
        &:empty
            padding-bottom 18px

</style>

<script>
import store from '@/store';
import Hashids from 'hashids';
import cx from 'classnames';
import Config from '@/config';

const hashidsGen = new Hashids();

const initialState = {
};

export default {
    name: 'RequestDocuments',
    data() {
        return {
            form: { ...initialState },
            modal: this.$select('modal') || {}
        };
    },
    props: {
        requestId: {
            type: String,
            'default': ''
        },
        loading: {
            type: Boolean,
            'default': false
        },
        blocks: {
            type: Array,
            'default': () => []
        },
        subBlocks: {
            type: Array,
            'default': () => []
        },
        fileNames: {
            type: Array,
            'default': () => []
        }
    },
    computed: {
        modifiedBlocks() {
            return this.blocks.map(singleItem => ({
                ...singleItem,
                state: this.subBlockByIdCheckSuccess(singleItem.id)
            }));
        }
    },
    methods: {
        documentsSuccess() {
            const countNotSuccess = this.subBlocks.filter(({ status }) => status !== 3);
            store.dispatch(
                store.reduxActions
                    .formDocumentsFileApprove(countNotSuccess.length - 1)
            );
        },
        documentsFail() {
            const countNotSuccess = this.subBlocks.filter(({ status }) => status !== 4);
            store.dispatch(
                store.reduxActions
                    .formDocumentsFileError(countNotSuccess.length - 1)
            );
        },
        subBlockById(blockId) {
            return this.subBlocks.filter(({ block }) => block === blockId);
        },
        subBlockByIdCheckSuccess(blockId) {
            return this.subBlocks
                .filter(({ block }) => block === blockId)
                .every(({ status }) => status === 3);
        },
        getHash(key) {
            return hashidsGen.encode(key);
        },
        filesByBlockSubblock(blockId, subBlockId) {
            return this.fileNames
                .filter(({ block, subBlock }) => (block === blockId && subBlock === subBlockId));
        },
        updateFileList(block, subBlock, newFiles) {
            const newFile = newFiles[0];
            const { name, lastModified, size } = newFile;
            const nameSplitted = name.split('.');
            const type = nameSplitted[nameSplitted.length - 1];
            const id = `${lastModified}-${size}`;
            store.dispatch(
                store.reduxActions
                    .formDocumentsAddFile({
                        newFile: true,
                        block,
                        subBlock,
                        name,
                        type,
                        id,
                        originalFile: newFile
                    })
            );
        },
        removeFile(fileId) {
            return () => {
                // this.fileNames = this.fileNames
                //     .filter(({ id }) => id !== fileId);
                store.dispatch(
                    store.reduxActions
                        .formDocumentsRemoveFile(fileId)
                );
            };
        },
        validateForm() {
            // eslint-disable-next-line
            this.form = { ...this.form, ...initialState };
        },
        subSectionClass(statusId) {
            const { label, noBackground } = Config.DOCUMENT_STATUSES
                .find(({ id }) => id === statusId) || {};
            return cx([ 'subblock-section', `subblock-section-${label}`, {
                [`subblock-section-${label}-no-bg`]: noBackground
            } ]);
        }
    }
};
</script>




