<template>
    <div class="holder">
        <div :class="'tooltip ' + (visible ? 'visible' : '')">{{text}}</div>
        <div @mouseover="mouseOver" @mouseout="mouseOut" class="content-holder" >
            <slot />
        </div>
    </div>
</template>

<style scoped lang="stylus">
    .holder
    .content-holder
        display inline
        position relative

    .tooltip
        position absolute
        top -20px
        background-color black
        padding 2px 3px
        border-radius 2px
        color white
        transform translateY(-5px)
        transition all .2s linear
        opacity 0
        white-space nowrap
        max-width 140px
        overflow hidden
        text-overflow ellipsis

    .tooltip.visible
        transform translateY(0)
        opacity 1

</style>

<script>
    export default {
        name: 'Tooltip',
        props: {
            text: {
                type: String,
                'default': ''
            }
        },
        data() {
            return {
                visible: false
            };
        },
        methods: {
            mouseOver() {
                this.visible = true;
            },
            mouseOut() {
                this.visible = false;
            }
        }
    };
</script>




