import { LOGIN, LOGOUT } from '../actions/auth';

const restoredState = JSON.parse(localStorage.getItem('auth'));

let intitialState = { authenticated: false };
if (restoredState) {
    intitialState = restoredState;
}

export default function auth(state = intitialState, action) {
    switch (action.type) {
        case LOGIN:
        case LOGOUT: {
            return { ...state, ...action.data };
        }

        default:
            return state;
    }
}



