<template>
  <div class="login-page">
    <div class="logo-holder">
        <VolvoLogo width="240"/>
        <SimpleSmall className="subtext">Лизинг грузовых автомобилей</SimpleSmall>
    </div>
    <div class="login-card">
        <Card className="centered">
            <div slot="card-body">
                <Heading className="heading-volvo">Вход в систему</Heading>
                <hr />
                <form @submit="loginToSystem" class="login-body">
                    <div class="line">
                        <FormLabel label="Логин" big>
                            <FormInput
                                v-model="login"
                                name="companyName"
                                placeholder=""
                                big />
                        </FormLabel>
                    </div>
                    <div class="line">
                        <FormLabel label="Пароль" big>
                            <FormInput
                                v-model="password"
                                name="soma-name"
                                placeholder=""
                                big
                                type="password"/>
                        </FormLabel>
                    </div>
                    <div class="line">
                        <FormLabel label="" flex>
                            <FormCheckbox name="no-fax" />&nbsp;&nbsp;<SimpleSmall black>запомнить меня</SimpleSmall>
                        </FormLabel>
                    </div>
                    <div class="login-button-holder">
                        <SimpleButton>Войти в систему</SimpleButton>
                    </div>
                </form>
            </div>
        </Card>
    </div>

  </div>
</template>

<style scoped lang="stylus">
    .heading-volvo
        padding 20px 30px 15px

    .login-page
        display flex
        justify-content center
        flex-direction column
        align-items center
        height 100%
        
    hr
        border 0
        height 0
        padding 0
        margin 0
        border-top 1px solid rgba(0, 0, 0, 0)
        border-bottom 1px solid rgba(0, 0, 0, .1)

    .login-card
        display flex
        justify-content center
        align-items center

    .login-body
        padding 15px 30px 30px

    .login-button-holder
        text-align center
        padding-top 10px

    .logo-holder
        position relative
        margin-bottom 25px

    .subtext
        position absolute
        top 57px
        left 20px
        // margin-bottom -30px
</style>

<script>
    import { required, sameAs } from 'vuelidate/lib/validators';
    import { login } from '@/actions/auth';
    import store from '@/store';

    export default {
        name: 'Login',
        data() {
            return {
                testLogin: 'test',
                testPassword: 'test',
                login: '',
                password: ''
            };
        },
        methods: {
            loginToSystem(event) {
                event.preventDefault();
                if (this.$v.$invalid) {
                    return false;
                }
                store.dispatch(login());
                this.$router.push('/');
                return true;
            }
        },
        validations: {
            login: {
                required,
                sameAs: sameAs('testLogin')
            },
            password: {
                required,
                sameAs: sameAs('testPassword')
            }
        }
    };
</script>



