<style scoped lang="stylus">
    @require '../../guide'

    .heading-volvo
        padding 22px 0 20px
        
    .two-fold
        display flex
        justify-content space-between

    .main-body
        width 60%

    .side-body
        width 30%

    .card-sub-section
        padding 20px
        margin -20px

        &:not(:first-child)
            border-top 1px solid $light-gray-separator-color

</style>

<script>
import Hashids from 'hashids';
import store from '@/store';
import { modalClose, modalOpen } from '@/actions/modal';

const initialState = {
    companyName: '',
    sameAddress: false,
    noFax: true
};
const hashidsGen = new Hashids();


export default {
    name: 'map',
    data() {
        return {
            form: { ...initialState },
            modal: this.$select('modal') || {},
            quickNavList: [
                { name: 'Основные сведения', id: 0, status: '0', current: false },
                { name: 'Коды', id: 1, status: '0', current: false },
                { name: 'Деятельность компании', id: 2, status: '0', current: false },
                { name: 'Учредители', id: 3, status: '0', current: false },
                { name: 'Контактная информация', id: 4, status: '0', current: false },
                { name: 'Банковский счет', id: 5, status: '0', current: false },
                { name: 'Грузовики/строительные машины, используемые в настоящее время', id: 6, status: '0', current: false }
            ]
        };
    },
    props: {
        formId: {
            // Порядковый номер анкеты
            type: String,
            'default': ''
        },
        requestId: {
            type: String,
            'default': ''
        }
    },
    methods: {
        getHash(key) {
            return hashidsGen.encode(key);
        },
        handleModalClose(modalId) {
            store.dispatch(modalClose(modalId));
        },
        openAddCommonPerson(event) {
            event.preventDefault();
            store.dispatch(modalOpen('newCommonPerson'));
        },
        openAddLegalPerson(event) {
            event.preventDefault();
            store.dispatch(modalOpen('newLegalPerson'));
        },
        openAddItem(event) {
            event.preventDefault();
            store.dispatch(modalOpen('newItem'));
        },
        validateForm() {
            // eslint-disable-next-line
            console.log(this.form.sameAddress);
            this.form = { ...this.form, ...initialState };
        }
    }
};
</script>




