<template>
    <div class="card-holder">
        <LoadingAnimation v-if="requests.loading" />
        <Card
            :shortCard="shortCard"
            :key="singleItem._id"
            v-for="singleItem in requests.list"
            v-if="requests.list.length && !requests.loading">

            <div slot="card-body" :class="'request-card ' + (shortCard ? 'request-card-short' : '')">
                <SimpleSmall block className="request-card-date">{{processDate(singleItem.date)}}</SimpleSmall>
                <div>
                    <SimpleLink className="request-card-link" noline router :href="prepeareLink(singleItem.number)">{{singleItem.number}}</SimpleLink>&nbsp;&nbsp;&nbsp;<ArrowRight/>
                </div>
                <div v-if="!shortCard">
                    <ul class="request-card-list">
                        <li v-for="item in singleItem.items" slot="item">{{item.name}}</li>
                        <li v-if="singleItem.more"><b>И еще {{singleItem.more}}</b></li>
                    </ul>
                    <div class="request-card-price">
                        <SimpleSmall block className="request-card-date">Стоимость предметов</SimpleSmall>
                        <Currency :amount="singleItem.price" :precision="0"/>
                    </div>
                    <div class="request-card-price">
                        <SimpleSmall block className="request-card-date">Общая сумма платежей</SimpleSmall>
                        <Currency className="request-card-price-big" :amount="singleItem.price"/>
                    </div>
                </div>
            </div>

            <div slot="card-footer" v-if="!shortCard">
                <table class="request-card-table">
                    <tr>
                        <td>Расчет</td>
                        <td><SimpleLink success router :href="prepeareLink(singleItem._id)">{{singleItem.count}}</SimpleLink></td>
                    </tr>
                    <tr>
                        <td>Анкета</td>
                        <td><SimpleLink attention router :href="prepeareLink(singleItem._id)">{{singleItem.form}}</SimpleLink></td>
                    </tr>
                    <tr>
                        <td>Документы</td>
                        <td><SimpleLink attention router :href="prepeareLink(singleItem._id)">{{singleItem.documents}}</SimpleLink></td>
                    </tr>
                </table>
            </div>
        </Card>
    </div>
</template>

<style scoped lang="stylus">

    .card-holder
        min-width 100%

    .request-card
        min-height 140px

        &-short
            min-height auto

        &-list
            list-style none
            margin 0
            font-size 14px
            min-height 76px
            padding 18px 0 0
            line-height 18px

        &-date
            color $gray-font-color

        &-link
            color $link-font-color
            font-size 23px
            padding-top 2px
            font-weight bold

        &-price
            display block
            padding-top 10px

            &-big
                font-weight bold
                font-size 20px

        &-table td:first-child
            color $gray-font-color

</style>

<script>
    import Card from '@/components/Card';
    import SimpleSmall from '@/components/SimpleSmall';
    import SimpleLink from '@/components/SimpleLink';
    import fecha from 'fecha';

    export default {
        name: 'RequestsList',
        components: {
            SimpleSmall,
            SimpleLink,
            Card
        },
        props: {
            requests: {
                type: Object,
                'default': {
                    requests: [],
                    loading: false
                }
            },
            shortCard: {
                type: Boolean,
                'default': false
            }
        },
        methods: {
            processDate(date) {
                return fecha.format(new Date(date), 'DD MMMM YYYY г.');
            },
            prepeareLink(id) {
                return `/request/${id}/calculations`;
            }
        }
    };
</script>




