<template>
  <Page>
    <Heading :size="2">Карта путей</Heading>
    <div v-for="route in routes">
        <SimpleLink router :href="route.example ? route.example : route.path">{{route.name}}</SimpleLink>
    </div>

  </Page>
</template>

<script>
    import Page from '@/components/Page';
    import SimpleLink from '@/components/SimpleLink';
    import Heading from '@/components/Heading';
    import { routerSettings } from '@/router/index';


    export default {
        components: {
            Page,
            Heading,
            SimpleLink
        },
        name: 'map',
        data() {
            return {
                routes: []
            };
        },
        mounted() {
            this.routes = routerSettings;
        }
    };
</script>




