<template>
    <span :class="'status ' + className + ' color-' + resultStatus.label">
        <span v-if="short && !numerical">
            <InProgress v-if="!resultStatus.success" />
            <GreenCheck v-if="resultStatus.success" />
        </span>
        <span v-if="numerical">{{percent}}%</span>
        <span v-if="!short && !numerical">
            <InProgress v-if="!resultStatus.success" />
            <GreenCheck v-if="resultStatus.success" />
            {{resultStatus.name}}</span>
    </span>
</template>

<style scoped lang="stylus">
    @require '../guide'
    .status
        text-transform lowercase
        font-size 14px

    .color-success
        color $link-success-color

    .color-attention
        color $link-attention-color

    .color-error
        color $link-error-color

</style>

<script>

export default {
    name: 'Status',
    props: {
        statuses: {
            type: Object,
            'default': () => ({
                success: {
                    name: 'Заполнено',
                    label: 'success',
                    success: true
                },
                fail: {
                    name: 'Не все документы загружены',
                    label: 'attention',
                    success: false
                }
            })
        },
        className: {
            type: String,
            'default': 'else'
        },
        successState: {
            type: Boolean,
            'default': false
        },
        'short': {
            type: Boolean,
            'default': false
        },
        numerical: {
            type: Boolean,
            'default': false
        },
        percent: {
            type: Number,
            'default': 0
        }
    },
    computed: {
        resultStatus() {
            if (this.successState) {
                return this.statuses.success;
            }
            return this.statuses.fail;
        }
    }
};
</script>




