<template>
    <div class="logo">
        <img :src="src" :alt="alt" @click.prevent="handleClick" :title="title" :class="className">
    </div>
</template>

<style scoped lang="stylus">

    .logo
        display flex
        justify-content center
        align-items center

</style>

<script>
export default {
    name: 'Logo',
    props: {
        src: {
            type: String,
            'default': 'http://www.fillmurray.com/100/100'
        },
        href: {
            type: String,
            'default': ''
        },
        className: {
            type: String,
            'default': ''
        },
        alt: {
            type: String,
            'default': 'No alt'
        },
        title: {
            type: String,
            'default': 'No title'
        }
    },
    methods: {
        handleClick() {
            if (!this.href) {
                return false;
            }
            window.open(this.href, '_blank');
            return true;
        }
    }
};
</script>




