<template>
  <Page>
        <!-- Заголовок -->
        <div class="heading-holder">
            <Heading className="heading-volvo">Заявка {{requestNumber}} от 31.01.2017</Heading>
            <nav>
                <SimpleLink
                    router
                    :className="subPage === 'calculations' ? 'current-nav' : ''"
                    :disabled="subPage === 'calculations'"
                    :href="subPage === 'calculations' ? '' : '/request/'+ this.$route.params.id + '/calculations'">Расчеты
                    <Bell />
                </SimpleLink>
                <SimpleLink
                    router
                    :className="subPage === 'forms' ? 'current-nav' : ''"
                    :disabled="subPage === 'forms'"
                    :href="subPage === 'forms' ? '' : '/request/'+ this.$route.params.id + '/forms'">
                    Анкеты
                    <GreenCircleCheck/>
                </SimpleLink>
                <SimpleLink
                    router
                    :className="subPage === 'documents' ? 'current-nav' : ''"
                    :disabled="subPage === 'documents'"
                    :href="subPage === 'documents' ? '' : '/request/'+ this.$route.params.id + '/documents'">
                    Документы
                    <Bell />
                </SimpleLink>
            </nav>
            <SimpleButton className="send-button" disabled>Отправить на рассмотрение</SimpleButton>
        </div>

        <!-- Анкеты -->
        <div v-if="subPage === 'forms' && !formId" class="forms">
            <RequestFormList :requestId="this.$route.params.id"/>
        </div>

        <!-- Анкета -->
        <div v-if="subPage === 'forms' && formId" class="forms">
            <RequestForm :formId="formId" :requestId="this.$route.params.id"/>
        </div>

        <!-- Документы -->
        <RequestDocuments
            v-if="subPage === 'documents'" class="documents"
            :blocks="formDocuments.blocks"
            :subBlocks="formDocuments.subBlocks"
            :fileNames="formDocuments.fileNames"
            :loading="formDocuments.loading"
            :requestId="this.$route.params.id"/>

        <!-- Рассчеты -->
        <div v-if="subPage === 'calculations'" class="calculations">
            <RequestBudget :requestId="this.$route.params.id"/>
        </div>
        <BackToTop>К списку предложений</BackToTop>
  </Page>
</template>

<style scoped lang="stylus">
    .send-button
        right -90px
        top 40px
        position absolute

    .heading
        &-volvo
            padding 0
            min-width 26%
            white-space nowrap

        &-holder
            display flex
            align-items center
            padding 46px 0 26px 0

    nav
        position absolute
        top 44px
        height 30px
        left 290px
        width 370px
        display flex
        justify-content space-between
        align-items center

        .current-nav
            position relative
            z-index 2
            &::after
                content: ''
                background #e4e6ea
                position absolute
                top: -7px
                left: -13px
                right: -18px
                bottom: -7px
                display block
                z-index -1

</style>

<script>
    import RequestForm from '@/pages/containers/RequestForm';
    import RequestBudget from '@/pages/containers/RequestBudget';
    import RequestFormList from '@/pages/containers/RequestFormList';
    import RequestDocuments from '@/pages/containers/RequestDocuments';

    export default {
        name: 'request',
        components: {
            RequestForm,
            RequestBudget,
            RequestFormList,
            RequestDocuments
        },
        mounted() {
            // store.dispatch(store.reduxActions.formDocumentsLoad());
        },
        data() {
            return {
                modal: this.$select('modal') || {},
                formDocuments: this.$select('formDocuments') || {}
            };
        },
        computed: {
            subPage() {
                return this.$route.params['0'].split('/')[0];
            },
            formId() {
                return this.$route.query.formId;
            },
            requestNumber() {
                return this.$route.params.id.split('-')[1];
            }
        },
        methods: {
        }
    };
</script>



