export const FORM_DOCUMENTS_LOAD = 'FORM_DOCUMENTS_LOAD';
export const FORM_DOCUMENTS_LOAD_SUCCESS = 'FORM_DOCUMENTS_LOAD_SUCCESS';
export const FORM_DOCUMENTS_LOAD_FAIL = 'FORM_DOCUMENTS_LOAD_FAIL';
export const FORM_DOCUMENTS_NEW_FILE_ADD = 'FORM_DOCUMENTS_NEW_FILE_ADD';
export const FORM_DOCUMENTS_FILE_REMOVE = 'FORM_DOCUMENTS_FILE_REMOVE';

export const FORM_DOCUMENTS_FILE_APPROVE = 'FORM_DOCUMENTS_FILE_APPROVE';
export const FORM_DOCUMENTS_FILE_ERROR = 'FORM_DOCUMENTS_FILE_ERROR';

export function formDocumentsLoading() {
    return { type: FORM_DOCUMENTS_LOAD };
}

export function formDocumentsLoaded(response) {
    return { type: FORM_DOCUMENTS_LOAD_SUCCESS, response };
}

export function formDocumentsAddFile(newFile) {
    return { type: FORM_DOCUMENTS_NEW_FILE_ADD, newFile };
}

export function formDocumentsRemoveFile(fileId) {
    return { type: FORM_DOCUMENTS_FILE_REMOVE, fileId };
}

export function formDocumentsFileApprove(fileId) {
    return { type: FORM_DOCUMENTS_FILE_APPROVE, fileId };
}

export function formDocumentsFileError(fileId) {
    return { type: FORM_DOCUMENTS_FILE_ERROR, fileId };
}

export function formDocumentsLoad(data) {
    return dispatch => {
        dispatch(formDocumentsLoading());
        setTimeout(() => {
            dispatch(formDocumentsLoaded(data));
        }, 500);
    };
}



