<template>
    <nav :class="computedClass">
        <h4>
            <StatusBinary className="overall-percent" :successState="percent === 100" numerical :percent="percent"/> Быстрая навигаця
        </h4>
        <ul class="quick-nav-list">
            <li v-for="(item, index) in modifiedList" :class="currentClosest === item.id ? 'current-item' : ''">
                <SimpleLink
                    :success="item.state"
                    black
                    className="status"
                    noline
                    :href="item.id"
                    :handleClick="handleClick">
                    <span class="status-name">{{item.name}}</span>
                    <StatusBinary className="status-value" :successState="Boolean(item.state)" short />
                </SimpleLink>
            </li>
        </ul>
    </nav>
</template>

<style scoped lang="stylus">
    @require '../guide'
    h4
        margin-bottom 10px
        padding 0 20px

    .current-item
        background-color white

    .status
        display flex
        align-items middle
        justify-items space-between
        &-name
            width 205px
            font-size 14px

    .overall-percent
        float right
        line-height 27px
        position relative
        top -5px
        right -8px

    .quick-nav
        width 260px
        position sticky
        top 25px
        padding 0

        &-list
            padding 0
            margin 0
            list-style none
            li
                padding 10px 20px
                line-height 20px

                &:not(:last-child)
                    border-bottom 1px solid $light-gray-separator-color
</style>

<script>
    import jump from 'jump.js';
    import Hashids from 'hashids';
    import cx from 'classnames';

    const hashidsGen = new Hashids();

    export default {
        name: 'BackToTop',
        props: {
            list: {
                type: Array,
                'default': () => [
                    { id: 0, name: 'Основные сведения', current: false, state: true },
                    { id: 1, name: 'Коды', current: true, state: false }
                ]
            }
        },
        data() {
            return {
                elOffset: 0,
                parents: [],
                jumping: false,
                currentClosest: null
            };
        },
        mounted() {
            document.addEventListener('scroll', this.handleQuickNavScroll);
            setTimeout(() => {
                this.parents = this.anchors();
            }, 1500);
        },
        beforeDestroy() {
            document.removeEventListener('scroll', this.handleQuickNavScroll);
        },
        methods: {
            getHash(key) {
                return hashidsGen.encode(key);
            },
            handleClick(event) {
                if (this.jumping) {
                    return false;
                }
                this.jumping = true;
                jump(`#${event.target.getAttribute('href')}`, {
                    offset: -2,
                    callback: () => {
                        this.jumping = false;
                    }
                });
                return false;
            },
            handleQuickNavScroll() {
                const offsets = this.parents.map(singleElement => {
                    const offset = singleElement.getBoundingClientRect().top;
                    return {
                        offset: offset < 0 ? 0 : offset,
                        id: singleElement.id
                    };
                })
                .filter(({ offset }) => offset !== 0)
                .sort((a, b) => a.offset - b.offset);
                this.currentClosest = offsets.length ? offsets[0].id : null;
            },
            anchors() {
                return this.list
                    .reduce((newArray, { id }) => newArray.concat([ this.getHash(id) ]), [])
                    .map(singleId => document.getElementById(singleId));
            }
        },
        computed: {
            percent() {
                const successes = this.list.filter(({ state }) => state);
                return (successes.length * 100) / (this.list.length || 1);
            },
            modifiedList() {
                return this.list
                    .map(singleItem => ({ ...singleItem, id: this.getHash(singleItem.id) }));
            },
            computedClass() {
                return cx([ 'quick-nav' ], {});
            }
        }
    };
</script>




