<template>
    <div class="pagination">
        <button class="single-page-link" :disabled="allowFirst" @click.prevent="gotoPage(1)">first</button>
        <button class="single-page-link" :disabled="allowPrev" @click.prevent="gotoPage(selectedPage - 1)">prev</button>
        <button :class="'single-page-link ' + isCurrentPage(page)" v-for="page in pageList" @click.prevent="gotoPage(page)">
            {{page}}
        </button>
        <button class="single-page-link" :disabled="allowNext" @click.prevent="gotoPage(selectedPage + 1)">next</button>
        <button class="single-page-link" :disabled="allowLast" @click.prevent="gotoPage(lastPage)">last</button>
    </div>
</template>

<style scoped>
</style>

<script>
export default {
    name: 'Pagination',
    props: {
        pageList: {
            type: Array,
            'default': () => [ 1, 2, 3, 4, 5 ]
        },
        allowFirst: {
            type: Boolean,
            'default': false
        },
        allowLast: {
            type: Boolean,
            'default': false
        },
        allowNext: {
            type: Boolean,
            'default': false
        },
        allowPrev: {
            type: Boolean,
            'default': false
        },
        lastPage: {
            type: Number,
            'default': 0
        },
        selectedPage: {
            type: Number,
            'default': 3
        }
    },
    methods: {
        gotoPage(pageNumber) {
            //eslint-disable-next-line
            console.log(pageNumber);
        },
        isCurrentPage(page) {
            return page === this.selectedPage ? 'single-page-link-current' : '';
        }
    }
};
</script>




